<template>
  <RetroType :lines="lines" :charDelay="charDelay" @printdone="printDone"></RetroType>
</template>
<script>
/** @typedef {{
 *  index: number,
 *  replacement: string,
 *  defaultValue?: string,
 *  replaceWith: (replacer: string) => void
 * }} CommandArg */

import Lines from "../content/lines";
import RetroType from "./RetroType.vue";
export default {
  components: {
    RetroType
  },
  props: {
    section: {
      type: String,
      default: "home"
    },
    arguments: {
      type: String,
      default: ""
    },
    charDelay: {
      type: Number,
      default: 4
    }
  },
  data: () => ({
    lines: []
  }),
  computed: {
    args() {
      if (!this.arguments || !this.arguments.trim()) return [];
      return [
        ...[...this.arguments].reduce(
          (words, char) => {
            if (char === '"') {
              if (words.quoteStarted) {
                words.quoteStarted = false;
                words.push("");
                return words;
              }
              words.quoteStarted = true;
              return words;
            }
            if (char === " " && !words.quoteStarted) {
              words.push("");
              return words;
            }
            words[words.length - 1] += char;
            return words;
          },
          [""]
        )
      ].filter(a => a);
    }
  },
  methods: {
    printDone() {
      this.$emit("retro-print-done");
    },
    handleSection(newSection) {
      if (!Lines.hasOwnProperty(newSection)) return;
      const linesOrigin = Lines[newSection];
      const lines = linesOrigin.copy ? linesOrigin.copy() : [...linesOrigin];
      const regex = /({(\d+):?([^}]+)?})/gi;
      /** @type CommandArg[]  */
      const args = [];
      for (const i in lines) {
        const line = lines[i];
        let arg = null;
        while ((arg = regex.exec(line))) {
          if (arg.length < 4) continue;
          const [replacement, , index, defaultValue] = arg;
          const replaceWith = (replacer = "") => {
            lines[i] = lines[i].replace(replacement, replacer);
          };
          args.push({ replacement, index, defaultValue, replaceWith });
        }
      }
      for (const arg of args) {
        if (false === arg.index in this.args) {
          arg.replaceWith(arg.defaultValue || "");
        }
        arg.replaceWith(this.args[arg.index]);
      }
      if (lines.withArgs && this.args.length > 0)
        lines.withArgs(lines, this.args);
      this.lines = [...lines];
    }
  },
  watch: {
    section: {
      immediate: true,
      handler(newSection) {
        this.handleSection(newSection);
      }
    },
    arguments(newArgs) {
      this.handleSection(this.section);
    }
  }
};
</script>
