/* eslint-disable no-console */
import Content from '../../components/Content.vue'
import { results, questions } from '../../content/test.json'
import SocialSharing from 'vue-social-sharing'

export default {
    components: {
        Content,
        SocialSharing,
    },

    data: () => ({
        charDelay: 8,
        section: 'test',
        cmd: 'test',
        currentQuestionNumber: 0,
        inputValue: '',
        commandList: ['a', 'b'],
        green: 0,
        red: 0,
        blue: 0,
        yellow: 0,
        result: false,
        typeCode: "",
        type: "",
        role: "",
        description: ""
    }),

    computed: {

        current() {
            return questions[this.currentQuestionNumber];
        },

        answerText() {
            return function (letter) {
                return letter + ". " + (this.current['answer_' + letter] || '').replace(/\n/g, '<br>');
            }
        },

    },

    methods: {
        testInit() {
            this.currentQuestionNumber = 0;
            this.green = 0;
            this.red = 0;
            this.blue = 0;
            this.yellow = 0;
            this.typeCode = "";
            this.type = "";
            this.role = "";
            this.result = false;
        },

        checkCmd(cmd) {
            if (cmd)
                cmd = cmd.toLowerCase().trim().replace(/[/-]/g, '');

            // Проверка на основные команды
            if (this.commandList.includes(cmd)) {
                this.lastCmd = cmd !== '' ? cmd : this.lastCmd;
                this.charDelay = 2;


                if (cmd === 'a')
                    this.calcCategory(this.current.category);

                if (this.currentQuestionNumber < 19)
                    this.currentQuestionNumber += 1;
                else this.showResult()
            }
            // Команды для выхода из Теста
            else if (['q', 'exit', 'home'].includes(cmd))
                this.$router.push({ name: 'command', params: { command: 'home' } })

            this.cmd = "";
            return true;
        },

        calcCategory(answer) {
            this[answer]++;
        },

        showResult() {
            const threshold = 3;
            const codeForType = [
                { type: 'green', false: 'I', true: 'E' },
                { type: 'red', false: 'N', true: 'S' },
                { type: 'blue', false: 'F', true: 'T' },
                { type: 'yellow', false: 'P', true: 'J' },
            ];

            for (const x of codeForType)
                this.typeCode += x[this[x.type] >= threshold];

            const { type, role, description } = results[this.typeCode];
            this.type = type;
            this.role = role;
            this.description = description;
            this.result = true;
            this.section = 'result';
        },

        skipTyping() {
            this.charDelay = 0;
            setTimeout(() => this.charDelay = 2, 50);
        },
    },
}