/**
 * @param {Array<string>} lines
 * @param {(lines: string[], args: string[]) => void} execArgs
 */
const withArgs = (lines, execArgs) => {
    const l = [ ...lines ];
    l.withArgs = execArgs;
    l.copy = () => withArgs([ ...lines ], execArgs);
    return l;
}
const random = (min, max) => Math.floor(Math.random() * (max - min + 1) + min);

